/*-------------------reset.css----------------------------*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a:hover {
  text-decoration: none;
}

ins, a {
  text-decoration: none;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  outline: 0;
}

input:focus, select:focus, textarea:focus, button:focus {
  outline: 0;
}

[contenteditable=true] {
  padding: 0;
  margin: 0;
  text-decoration: none;
  outline: none;
}

@keyframes lightSpeedIn {
  from {
    transform: translate3d(-100%, 0, 0);
    opacity: 0;
  }
  60% {
    opacity: 0.5;
  }
  80% {
    opacity: 1;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeIn {
  from {
    opacity: .5;
    left: 50%;
    transform: translate(-50%, -10px);
  }
  25% {
    opacity: .625;
  }
  50% {
    opacity: .75;
  }
  75% {
    opacity: .875;
  }
  to {
    opacity: 1;
    transform: translate(-50%, 0px);
  }
}

@keyframes fadeInDown {
  from {
    opacity: .5;
    transform: translateY(20px);
  }
  25% {
    opacity: .625;
  }
  50% {
    opacity: .75;
  }
  75% {
    opacity: .875;
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateY(0px);
  }
  to {
    opacity: 0;
    transform: translateY(20px);
  }
}

@keyframes changeColor {
  from {
    background-color: rgba(250, 214, 137, 0.3);
  }
  to {
    background-color: #FFF;
  }
}

.fadeIn {
  animation-name: fadeIn;
  animation-timing-function: ease-out;
  animation-duration: 0.2s;
}

.fadeInDown {
  animation-name: fadeInDown;
  animation-timing-function: ease-out;
  animation-duration: 0.2s;
}

.fadeOut {
  animation-name: fadeOut;
  animation-timing-function: ease-out;
  animation-duration: 0.25s;
}

.changeColor {
  animation-name: changeColor;
  animation-timing-function: ease-out;
  animation-duration: 0.4s;
}

.lightSpeedIn {
  animation-name: lightSpeedIn;
  animation-timing-function: ease-out;
  animation-duration: 1.0s;
}

.newanime-date-area .date-block .date-block-border {
  right: 0px;
  width: calc(100%);
}

.newanime-date-area:nth-of-type(6n) .date-block .date-block-border {
  right: 0px;
  width: calc(100%);
}

.newanime-date-area:nth-of-type(6n) .date-block .dotted-line {
  width: calc( 100%);
}

.newanime-container {
  position: relative;
  width: 100%;
}

.newanime-container .newanime-wrap {
  margin: auto;
  padding: 64px 0;
  max-width: 1380px;
  box-sizing: border-box;
}

.newanime-container .newanime-wrap .newanime-block {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 16px 12px 0 12px;
  overflow: hidden;
}

.newanime-date-area {
  display: flex;
  flex-direction: column;
  margin: 0 0 60px 0;
  width: calc(100% / 6);
}

.newanime-date-area .date-block {
  position: relative;
  height: 40px;
}

.newanime-date-area .date-block > span {
  position: absolute;
  top: -12px;
  right: 12px;
  font-size: 13px;
  color: #fff;
  padding: 4px 16px;
  text-align: center;
  border-radius: 5px;
  background-color: #969696;
  z-index: 3;
}

.newanime-date-area .date-block .double-circle {
  position: absolute;
  top: -9px;
  right: -10px;
  z-index: 3;
}

.newanime-date-area .date-block .double-circle > .circle {
  fill: white;
  stroke: #c4c4c4;
}

.newanime-date-area .date-block .circle-active > .circle {
  stroke: #FB9C00;
}

.newanime-date-area .date-block .date-info {
  top: -12px;
  right: unset;
  left: 50%;
  transform: translateX(-50%);
  font-size: 13px;
  pointer-events: none;
  background-color: #FB9C00;
}

.newanime-date-area .date-block .date-block-border {
  position: absolute;
  top: 0px;
  height: 3px;
  background-color: #c4c4c4;
  z-index: 1;
}

.newanime-date-area .date-block .border-changecolor {
  top: -1px;
  background-color: #FB9C00;
  height: 5px;
  width: calc(100%);
  z-index: 2;
}

.newanime-date-area .date-block .dotted-line {
  position: absolute;
  top: 30px;
  left: 0;
  width: 100%;
}

.newanime-date-area .anime-block {
  position: relative;
  padding: 24px 12px 0px 12px;
}

.newanime-date-area .anime-block > span {
  position: absolute;
  top: -14px;
  left: 50%;
  transform: translateX(-50%);
  padding: 4px 16px;
  font-size: 13px;
  color: #fff;
  text-align: center;
  border-radius: 50px;
  background-color: #56b4d1;
}

.newanime-date-area .anime-block .anime-card-block {
  display: flex;
  flex-direction: column;
  width: 100%;
  background-color: #969696;
  border-radius: 3px;
  overflow: hidden;
  box-shadow: 0 0 6px 1px rgba(0, 0, 0, 0.1);
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block {
  position: relative;
  width: 100%;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .line-gradient {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 23%, rgba(0, 0, 0, 0.3) 64%, rgba(0, 0, 0, 0.6) 100%);
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-blocker {
  position: relative;
  background-color: #fff;
  overflow: hidden;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-blocker:after {
  content: "";
  position: relative;
  display: block;
  width: 100%;
  padding-bottom: 56.25%;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-blocker > img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-detail-info-block {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  width: 100%;
  padding: 4px;
  box-sizing: border-box;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-detail-info-block .anime-episode {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  flex: 1;
  padding: 4px;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-detail-info-block .anime-episode > img {
  margin: 0 4px 0 0;
  width: 12px;
  height: 12px;
  fill: #fff;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-detail-info-block .anime-episode > p {
  font-size: 12px;
  color: #fff;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .anime-detail-info-block .new-anime {
  padding: 0px 8px;
  height: 20px;
  font-size: 13px;
  border-radius: 4px;
  color: #fff;
  font-weight: 500;
  background-color: #DB5539;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .btn-favorite {
  position: absolute;
  top: 4px;
  right: 4px;
  width: 30px;
  height: 30px;
}

.newanime-date-area .anime-block .anime-card-block .anime-pic-block .btn-favorite > img {
  width: 100%;
  height: 100%;
}

.newanime-date-area .anime-block .anime-card-block .anime-name-block {
  display: flex;
  flex-direction: row;
  padding: 4px 8px;
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
}

.newanime-date-area .anime-block .anime-card-block .anime-name-block .anime-name {
  width: calc(100% - 80px);
  flex-grow: 1;
  overflow: hidden;
}

.newanime-date-area .anime-block .anime-card-block .anime-name-block .anime-name > p {
  font-size: 13px;
  line-height: 18px;
  color: #323232;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipse;
}

.newanime-date-area .anime-block .anime-card-block .anime-name-block .anime-watch-number {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  max-width: 80px;
  overflow: hidden;
}

.newanime-date-area .anime-block .anime-card-block .anime-name-block .anime-watch-number > img {
  margin: 0 4px;
  width: 14px;
  height: 14px;
}

.newanime-date-area .anime-block .anime-card-block .anime-name-block .anime-watch-number > p {
  font-size: 13px;
  line-height: 18px;
  color: #323232;
}

.new-count-1 {
  z-index: 4;
}

.new-count-1 .date-block .date-block-border {
  background-color: #FB9C00;
}

.new-count-1 .date-block > span {
  background-color: #FB9C00;
}

.new-count-2 {
  z-index: 3;
}

.new-count-3 {
  z-index: 2;
}

.new-count-4 {
  z-index: 1;
}
